<template>
  <div class="line-container" style="opacity: 1; transform: translateX(0px) scale(1);width: 100%;">
    <div class="line">
      <div
        class="profile-img"
        style="opacity: 1; transform: translateY(0px);background-size: cover;background-position: center"
        :style="'background-image: url(' + c.userImg + ');'"
      ></div>
      <div class="body">
        <div class="name text-info" style="opacity: 1; transform: translateY(0px);">
          {{c.owner}}
          <span class="small text-secondary">{{calendarTime(c.createdTime)}}</span>
        </div>
      </div>
      <div
        class="text"
        style="white-space: pre-wrap;width: 90%; opacity: 1; transform: translateY(0px);    margin-left: 70px;margin-top: -40px;width: 90%;"
        v-emoji:message="c.message"
      ></div>
    </div>
  </div>
</template>

<script>
import { convertDate } from "../utilities/index";

export default {
  name: "ChatMessage",
  props: {
    c: Object
  },
  methods: {
    calendarTime: convertDate
  }
};
</script>


<style scoped>
.user-image {
  height: 40px;
  width: 40px;
}
hr {
  width: 100%;
}

.line-container {
  /* overflow: hidden; */
  border-radius: 6px;
  /* max-height: 0px; */
  /* opacity: 0; */
  -webkit-transform: translateX(-300px) scale(0.2);
  transform: translateX(-300px) scale(0.2);
  transition: margin-bottom 200ms, max-height 500ms, opacity 100ms,
    -webkit-transform 250ms;
  transition: margin-bottom 200ms, max-height 500ms, opacity 100ms,
    transform 250ms;
  transition: margin-bottom 200ms, max-height 500ms, opacity 100ms,
    transform 250ms, -webkit-transform 250ms;
  transition-timing-function: ease-out;
}

.line {
  padding: 10px;
  /* background-color: #242424; */
}
.line > div {
  display: inline-block;
  vertical-align: top;
}

.profile-img {
  cursor: pointer;
  border-radius: 6px;
  width: 60px;
  height: 60px;
  background-color: #383838;
  margin-right: 10px;
}

.body .name,
.body .text {
  border-radius: 2px;
  /* background-color: #474747; */
  /* height: 16px; */
}

.name {
  width: 200px;
  margin-bottom: 10px;
  position: relative;
  cursor: pointer;
}
/* .name:after {
  content: "";
  display: block;
  border-radius: 2px;
  background-color: rgba(56, 56, 56, 0.4);
  height: 16px;
  width: 50px;
  position: absolute;
  right: -60px;
  transition: 100ms;
} */

/* .profile-img:hover + .body .name:after,
.name:hover:after {
  background-color: #383838;
  width: 100px;
  right: -110px;
} */

.text:not(:last-child) {
  margin-bottom: 10px;
}

.rich-body {
  margin-left: 14px;
  margin-top: 36px;
  position: relative;
}
.rich-body:before {
  content: "";
  display: block;
  position: absolute;
  top: -26px;
  left: -14px;
  bottom: 0;
  width: 4px;
  background-color: inherit;
}
.rich-body:after {
  content: "";
  display: block;
  position: absolute;
  height: 16px;
  width: 200px;
  top: -26px;
  background-color: inherit;
  border-radius: 2px;
}

.img,
.rich-body {
  width: 300px;
  height: 300px;
  cursor: pointer;
  border-radius: 6px;
  background-color: #333333;
}

.profile-img,
.name,
.text,
.img,
.rich-body {
  opacity: 0;
  -webkit-transform: translateY(20px);
  transform: translateY(20px);
  transition: 200ms;
}
</style>



